// Вызов миксина с передачей аргументов. Передаем разные
// начертания (файлы) для разных `font-weight`.
@import './_mixins/_font-face';

@include font-face('Museo', '../fonts/Museo/light', $weight: 300);
@include font-face('Museo', '../fonts/Museo/Regular');
@include font-face('Museo', '../fonts/Museo/Medium', $weight: 600);
@include font-face('Museo', '../fonts/Museo/Bold', $weight: 800);
@include font-face('Museo', '../fonts/Museo/Black', $weight: 900);

// Назначение главного шрифта
body {
	font-family: 'Museo', sans-serif; }

// Все эти куски должны быть в трех разных файлах:
// – Миксин – в `sass/mixins/fonts`
// – Вызов – в `sass/_fonts` (куда импортирутеся миксин)
// – Назначение – в `sass/_base` (куда импортирутеся вызов)
