@import '../sass/_vars';


.catalog {
    &__site {
        background-color: $light-gray; }
    &__work {
        background: $white;
        &__01 {
            background-image: url("../img/catalog/01.png");
            background-repeat: no-repeat;
            height: 102px; } }
    &__red {
        background: $primary;
        &__color-white {
            color: $white; } }

    &__gray {
        background: $white;
        padding-top: 4rem;
        padding-bottom: 4rem; }
    &__bg-white {
        background: $white; }
    &__img-left-bar {
        background-image: linear-gradient(180deg,rgba(8,12,37,.2) -18.91%,rgba(25, 29, 43, 0.71) 57.57%),url(../img/catalog/image92.png);
        background-repeat: no-repeat;
        width: 100%;
        height: 451px;
        background-size: cover;
        &__info {
            width: 100%;
            h4 {
                margin-bottom: 10px; }
            button {
                margin-top: 35px;
                width: 100%; } } }

    &__list {
        &__nav-link {
            color: $dark-gray;
            &:hover {
                color: $primary; } } }
    &__button {
        button {
            width: 100%; } } }


.site-catalog {
    background-color: $light-gray; }

.custom-control-label {
    font-size: 11px; }

