.half-height {
    height: 550px;
    @include respond-to(phone-land) {
        height: 500px; }

    @include respond-to(phone) {
        height: 85vh; } }

.actions-slide1 {
    background-image: url('../img/actions/first-slide.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/first-slide-mobile.png'); }

    &__title {
        width: 70%;
        @include respond-to(phone-land) {
            width: 100%; } }

    &__description {
        width: 70%;
        @include respond-to(phone-land) {
            width: 100%; } }

    &__rectangle {
        left: 0;
        max-width: 50%;
        height: 100%;
        @include respond-to(phone-land) {
            background-image: url('../img/actions/first-slide-mobile.png');
            transform: rotate(90deg);
            max-width: 100%;
            top: 0; } }

    &__exclamation-point {
        background: rgba(228, 228, 228, 0.20);
        width: 20px;
        height: 20px;
        border-radius: 50%; }

    &__cart {
        right: 0;
        max-width: 50%;
        @include respond-to(phone-land) {
            bottom: 0;
            max-width: 100%; } } }

.actions-slide2 {
    background-image: url('../img/actions/second-slide.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/second-slide-mobile.png'); }

    &__text {
        margin-bottom: 200px; } }

.actions-slide3 {
    background-image: url('../img/actions/slide3.png');
    background-position: left;
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide3-mobile.png'); }

    &__text {
        width: 50%;
        @include respond-to(laptop) {
            width: 80%; } } }

.actions-slide4 {
    background-image: url('../img/actions/slide4.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide4-mobile.png'); }

    &__text {
        width: 80%;
        @include respond-to(phone-land) {
            margin-bottom: 100px;
            width: 100%; } } }

.actions-slide5 {
    background-color: $gray;
    background-image: url('../img/actions/slide5.png');
    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide5-mobile.png'); }
    &__text {
        font-size: 1.5rem; } }

.actions-slide6 {
    background-color: $black;
    background-image: url('../img/actions/slide6.png');
    background-position: left;
    @include respond-to(laptop) {
        background-position: bottom; }

    @include respond-to(phone-land) {
        background-image: url('../img/actions/slide6-mobile.png'); }
    &__text {
        font-size: 1.5rem; } }


/***************** End of Action slides  *************/

.catalog-sorting {
    margin-left: 225px; }

.actions-sidebar {
    min-width: 280px;
    margin-right: 30px;

    .card-body {
        margin-bottom: 30px; }

    .filter {
        padding: 30px 20px; }

    .filter-content li:first-child i {
        cursor: pointer;
        font-size: 14px;
        border: 2px solid $light-gray;
        color: $primary; }

    .filter-content li:first-child {
        color: $primary; }

    .fa-square {
        border: 2px solid $light-gray; } }




/***************** Slider Price Range *************/
.ui-widget.ui-widget-content {
    border: none;
    background: $light-gray;
    height: 3px; }

.ui-slider-horizontal .ui-slider-range {
    background: $primary; }

.ui-state-default, .ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default, .ui-button,
html .ui-button.ui-state-disabled:hover,
html .ui-button.ui-state-disabled:active {
    border: none;
    border-radius: 50%;
    background: $primary;
    outline: none;
    transform: translateY(-3px); }

/*****************  Filter Accordion *************/
.panel-title,
.panel-body {
    margin-bottom: 2rem; }

.panel-title {
    cursor: pointer; }

/***************** End of price range *************/

.filter-price-range {
    .price-value {
        background: $light-gray;
        padding: 8px 10px; } }


.price-range__value-low {
    top: -42px;
    left: -16px;
    color: $dark-gray; }

.price-range__value-up {
    top: -42px;
    right: -37px;
    color: $dark-gray; }

.product-info {
    p {
        padding-bottom: 10px; } }

.hidden {
    display: none; }


.active {
    color: $primary; }

.actions-pagination {
    &__turn-left,
    &__turn-right {
        height: 25px;
        width: 25px;
        background: $gray;
        color: $white; } }

/***************** Filter Price Checkboxes *************/
/* The container */
.checkbox-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer; }
//   font-size: 22px
//   -webkit-user-select: none
//   -moz-user-select: none
//   -ms-user-select: none
//   user-select: none

/* Hide the browser's default checkbox */
.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: $light-gray; }

/* Highlight container on hover */
.checkbox-container:hover {
    color: $primary; }

/* On mouse-over, add a grey background color */
.checkbox-container:hover input ~ .checkmark {
  background-color: $primary;
  height: 20px;
  width: 20px;
  border: 4px solid $light-gray; }

/* When the checkbox is checked, add a blue background */
.checkbox-container input:checked ~ .checkmark {
  background-color: $primary;
  height: 20px;
  width: 20px;
  border: 4px solid $light-gray; }

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.checkbox-container input:checked ~ .checkmark:after {
  display: block; }
